@import "../../assets/scss/variable.scss";
$trs: all ease 0.3s 0s;

.card-wrapper {
	margin: 0 0 30px;
	padding: 0 5px;
	color: black;
	height: fit-content;
	a {
		h3 {
			font-size: 2.3vh;
		}
		h3:hover {
			font-weight: bold;	
		} 	
	}
	.card {
		margin: 0 0 15px;
		border: none;
		border-radius: 0px;
		box-shadow: 0.5vh 0.5vh 1vh #000000bd;
		width:100%;
		text-align: center;
		min-height: 150px;
		display: flex;
		justify-content: center;
		.desc-card {
			z-index: 20;
			padding: 5px 7px;
			background-color: white;
			color: black;
			width: 95%;
			height: 95%;
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			margin: auto;
			box-shadow: 0.5vh 0.5vh 1vh #000000bd;
			overflow: auto;
			opacity: 0;
			transition: $trs;
			-moz-transition: $trs;
			-webkit-transition: $trs;
			h3 {
				text-align: center;
				font-size: 1.4rem;
			}
			p {
				margin-bottom: 0.6rem;
			}
			a {
				text-align: left;
				width: fit-content;
			}
			.cate {
				text-align: center;
			}
		}
		.empty-thumb {
			position: relative;
			height: 0;
			padding-bottom: 56.25%;
			display: flex;
			justify-content: center;
			span {
				position: absolute;
				top: 42%;
			}
		}
	}
}
.mobile-btn {
	font-size: 2vh;
    border-bottom: 2px solid;
    width: fit-content;
    line-height: 1;
}
.ifram-wrapper {
	position:relative;
	height:0;
	padding-bottom:56.25%;
}
.work-video {
	max-width: 100%;
	position:absolute;
	width:100%;
	height:100%;
	left: 0;
}
.work-img {
	max-width: 100%;
}

//== DEVICE SCREEN ==//
/*대부분의 스마트폰 모바일 기기(세로)*/
@media (min-width: 320px) and (max-width: 480px) {
	.card {
		box-shadow: 0.2vh 0.2vh 0.5vh #000000bd!important;
	}
}

