@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body{--bkg-color: #fff;--text-color: #222;--text-color-blue: Blue}body.dark-theme{--text-color: #eee;--text-color-blue: #0078ff;--bkg-color: #121212;--bkg-color-2: black}@media (prefers-color-scheme: dark){body{--text-color: #eee;--text-color-blue: #0078ff;--bkg-color: #121212;--bkg-color-2: black}body.light-theme{--text-color: #222;--text-color-blue: Blue;--bkg-color: #fff;--bkg-color-2: white}}body,h1,h2,h3,p,span,div{transition:all ease 0.2s 0s;-moz-transition:all ease 0.2s 0s;-webkit-transition:all ease 0.2s 0s}body{background-color:var(--bkg-color)}h1,h2,h3,p,span,div{color:var(--text-color) !important}#Nav .active{border-bottom:2px solid var(--text-color) !important}.footer{color:var(--text-color)}.footer .lang-toggle-box .active{border-color:var(--text-color) !important}.footer .dark-active{border-bottom:2px solid var(--text-color) !important}.card-wrapper .card{background-color:var(--bkg-color-2)}.card-wrapper .card .desc-card{background-color:var(--bkg-color) !important}.card-wrapper .card .empty-thumb{background-color:var(--bkg-color-2)}#root,.App,#Home{height:100vh}body{min-height:100vh;max-width:100vw;margin:0;overflow:hidden}.container{max-width:100% !important}.row{height:100%}.col-md-3{margin-bottom:40px;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.layout{z-index:0;width:100%}.view-layout{height:100%;width:100%;overflow:hidden;word-wrap:break-word}.pd-12{padding:0 12px}p,a,h1,h2,h3{font-size:medium;font-size:initial;font-weight:500;letter-spacing:normal;letter-spacing:initial}ul,div{font-weight:500;letter-spacing:normal;letter-spacing:initial}p{line-height:1.55;text-align:left;margin-bottom:2rem}a{color:var(--text-color-blue);text-decoration:underline;line-height:1.55;transition:all ease 0.1s 0s;-moz-transition:all ease 0.1s 0s;-webkit-transition:all ease 0.1s 0s}a:hover{color:var(--text-color-blue);text-decoration:none;transition:all ease 0.1s 0s;-moz-transition:all ease 0.1s 0s;-webkit-transition:all ease 0.1s 0s}ul{padding:0;margin:0;list-style:none}ul li{font-size:medium;font-size:initial}ul li a{letter-spacing:normal;letter-spacing:initial;font-weight:500}::selection{color:var(--bkg-color);background-color:#e10000}::-moz-selection{color:var(--bkg-color);background-color:blue}.ft-s-s p,.ft-s-s a{font-size:medium;font-size:initial;line-height:1.55}.ft-s-ss p,.ft-s-ss a{font-size:medium;font-size:initial;line-height:1.55}a .f-n{color:var(--text-color-blue) !important}.f-n{font-size:.65rem;vertical-align:6px;-webkit-text-stroke:0.2px}.f-n-s{font-size:.5rem;vertical-align:6px;-webkit-text-stroke:0.2px}.t-underline{text-decoration:underline}.outLine{border:2px solid var(--text-color);padding:10px;overflow:auto;max-height:95%;margin-top:80px}.bottomLine{display:inline;font-size:medium;font-size:initial;border-bottom:2px solid var(--text-color)}@media (min-width: 320px) and (max-width: 480px){.view-layout{padding:0 7px}}@media (min-width: 481px) and (max-width: 767px){.view-layout{padding:0 7px}}

.footer{position:fixed;bottom:0;width:100%;text-align:center;font-size:1rem !important;margin-bottom:5px;font-weight:600;padding-bottom:0.5rem}.footer .lang-toggle-box{margin-bottom:0.2rem}.footer .lang-toggle-box .lang-toggle{margin:0 5px;cursor:pointer}.footer .lang-toggle-box .lang-toggle:hover{border-bottom:2px solid black}.footer .lang-toggle-box .active{border-bottom:2px solid black}.footer .dividing-line{margin:0 5px}.footer .dark-toggle-box .dark-toggle{margin:0 5px;cursor:pointer}

#Nav{z-index:1;position:fixed;top:6px;padding:5px 15px;display:flex;align-items:center;line-height:1;width:100%;overflow:auto;-ms-overflow-style:none;scrollbar-width:none;min-height:65px}#Nav .nav-toggle{margin:2px;font-weight:400;letter-spacing:2px;transition:all ease 0.1s 0s;-moz-transition:all ease 0.1s 0s;-webkit-transition:all ease 0.1s 0s;cursor:pointer}#Nav .nav-toggle h1{margin:0;font-weight:500;font-size:1.4rem}#Nav .nav-toggle a{margin-top:0.7rem;display:block}#Nav .active{border-bottom:2px solid black}#Nav .inactive{padding:4px 8px 4px 7px}#Nav .inactive h1{font-size:medium;font-size:initial}#Nav a{font-size:medium;font-size:initial}#Nav::-webkit-scrollbar{display:none}@media (min-width: 320px) and (max-width: 480px){#Nav{padding:2px 5px}}

.section{width:100%;height:100%;position:fixed;transition:all ease 0.3s 0s;-moz-transition:all ease 0.3s 0s;-webkit-transition:all ease 0.3s 0s}.visible{opacity:1}.invisible{opacity:0;transform:scale(0.95);-moz-transform:scale(0.95);-webkit-transform:scale(0.95);-ms-transform:scale(0.95)}

.about-mid{padding:80px 10px 40px;height:100vh;overflow:auto;overflow-x:hidden;text-align:center}.title-center{display:block;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin:auto}.about-center{text-align:center;word-break:keep-all}.about-right{margin-top:80px}@media (min-width: 320px) and (max-width: 480px){.about-mid{padding:0 20px;height:auto;height:initial;top:auto;top:initial}.about-right{padding:0 20px;padding-bottom:60px;margin-top:10px}}@media (min-width: 481px) and (max-width: 767px){.about-mid{padding:0 20px;height:auto;height:initial;top:auto;top:initial}.about-right{padding:0 20px;padding-bottom:60px;margin-top:10px}}

.card-wrapper{margin:0 0 30px;padding:0 5px;color:black;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.card-wrapper a h3{font-size:2.3vh}.card-wrapper a h3:hover{font-weight:bold}.card-wrapper .card{margin:0 0 15px;border:none;border-radius:0px;box-shadow:0.5vh 0.5vh 1vh #000000bd;width:100%;text-align:center;min-height:150px;display:flex;justify-content:center}.card-wrapper .card .desc-card{z-index:20;padding:5px 7px;background-color:white;color:black;width:95%;height:95%;position:absolute;top:0;bottom:0;left:0;right:0;margin:auto;box-shadow:0.5vh 0.5vh 1vh #000000bd;overflow:auto;opacity:0;transition:all ease 0.3s 0s;-moz-transition:all ease 0.3s 0s;-webkit-transition:all ease 0.3s 0s}.card-wrapper .card .desc-card h3{text-align:center;font-size:1.4rem}.card-wrapper .card .desc-card p{margin-bottom:0.6rem}.card-wrapper .card .desc-card a{text-align:left;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.card-wrapper .card .desc-card .cate{text-align:center}.card-wrapper .card .empty-thumb{position:relative;height:0;padding-bottom:56.25%;display:flex;justify-content:center}.card-wrapper .card .empty-thumb span{position:absolute;top:42%}.mobile-btn{font-size:2vh;border-bottom:2px solid;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;line-height:1}.ifram-wrapper{position:relative;height:0;padding-bottom:56.25%}.work-video{max-width:100%;position:absolute;width:100%;height:100%;left:0}.work-img{max-width:100%}@media (min-width: 320px) and (max-width: 480px){.card{box-shadow:0.2vh 0.2vh 0.5vh rgba(0,0,0,0.741176) !important}}

.row-px{width:100%;height:100vh;position:absolute;padding:80px 0;padding-right:20px;overflow:scroll;-webkit-overflow-scrolling:touch;z-index:5}.row-px .scroll-info{width:100%;text-align:center;position:absolute;z-index:1;font-weight:bold;margin:auto;font-size:2rem;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}@media (min-width: 320px) and (max-width: 480px){.row-px{padding:0 10px 20px;padding-bottom:30px;width:auto;width:initial;height:100%;position:static;position:initial}}@media (min-width: 481px) and (max-width: 767px){.row-px{padding:0 10px 20px;padding-bottom:30px;width:auto;width:initial;height:100%;position:static;position:initial}}


#Detail{height:100vh;overflow:auto}.detail-layout{margin:auto;text-align:center;padding:0 5px}.detail-layout .view-layout{position:fixed;height:100%;left:0}.detail-layout .view-layout .content-section{width:100%;height:100vh;overflow:scroll;padding-top:80px;padding-bottom:20px}.detail-layout .container{padding:1rem 0;margin-bottom:5vh;max-width:1200px !important}.detail-layout .container .desc{padding:0 5px;margin-bottom:40px}.detail-layout .container .desc p{text-align:center}.detail-video-wrapper{position:relative;height:0;padding-bottom:100%;margin:1px 0}.detail-video{max-width:100%;position:absolute;width:100%;height:100%;left:0}.detail-img{max-width:100%}.pd-03{padding:0 3px}@media (min-width: 320px) and (max-width: 480px){.pd-03{padding:0}}@media (min-width: 481px) and (max-width: 767px){.pd-03{padding:0}}

